html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:root {
  --main-black: #202020;
  --white-color: #fff;
  --hover-color: #FF6E30;
  --focus-color: #BC572B;
  --active-color: #CE4307;
  --cacaca-color: #cacaca;
  --e9e9e9-color: #e9e9e9;
  --999-color: #999;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:focus-visible {
  outline: none;
}

*:focus {
  outline: none;
}

a {
  position: relative;
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

@font-face {
  font-family: "TTFirsNeue";
  src: local("TTFirsNeue"), url(../fonts/TTFirsNeue-Regular.woff2) format("woff2"), url(../fonts/TTFirsNeue-Regular.woff) format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TTFirsNeue";
  src: local("TTFirsNeue"), url(../fonts/TTFirsNeue-Bold.woff2) format("woff2"), url(../fonts/TTFirsNeue-Bold.woff) format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url(../fonts/Roboto-Regular.woff2) format("woff2"), url(../fonts/Roboto-Regular.woff) format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url(../fonts/Roboto-Bold.woff2) format("woff2"), url(../fonts/Roboto-Bold.woff) format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  font-family: "TTFirsNeue", sans-serif;
  font-weight: 400;
}

.container {
  max-width: 1352px;
  margin: 0 auto;
  padding-left: 22px;
  padding-right: 39px;
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.my-btn {
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
  background: transparent;
}

.hashtag {
  position: absolute;
  align-self: flex-start;
  top: 15px;
  right: 15px;
  padding: 6px 14px;
  background-color: #333;
  font-size: 14px;
  line-height: 16px;
  color: var(--white-color);
}

.hero__btn,
.hero__descr,
.about-us__descr,
.about-us__btn,
.projects__link,
.projects__day,
.projects__descr,
.projects__btn,
.contacts__adr,
.contacts__input,
.contacts__textarea,
.contacts__content,
.contacts__btn,
.footer__more {
  font-family: "Roboto", "sans-serif";
}

.hero__title,
.hero__btn,
.hero__subtitle,
.about-us__title,
.about-us__btn,
.projects__title,
.projects__subtitle,
.projects__btn,
.contacts__btn {
  font-weight: 700;
}

.hero__btn,
.hero__descr,
.about-us__btn,
.projects__btn,
.contacts__input,
.contacts__textarea {
  font-size: 16px;
  line-height: 19px;
}

.projects__link,
.projects__descr,
.contacts__content,
.footer__quote,
.footer__name,
.footer__more {
  font-size: 16px;
  line-height: 22px;
}

.header {
  background-color: var(--main-black);
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 20px;
}
.header__list {
  display: flex;
  padding-left: 47px;
}
.header__list-item {
  font-size: 16px;
  line-height: 21px;
  color: var(--white-color);
  transition: color 0.3s ease;
}
.header__list-item:not(:last-child) {
  margin-right: 79px;
}
.header__list-item:hover {
  color: var(--hover-color);
}
.header__list-item:focus-visible {
  background-color: var(--focus-color);
}
.header__list-item:active {
  color: var(--active-color);
}
.header__social-list {
  display: flex;
}
.header__social-item:not(:last-child) {
  margin-right: 20px;
}
.header__search-btn {
  display: none;
}

.social-img {
  display: inline-block;
  width: 30px;
  height: 30px;
}

.bottom {
  display: none;
}

.bottom__burger {
  display: none;
}

.hero {
  padding-top: 41px;
  padding-bottom: 63px;
}
.hero__container, .hero__list {
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(2, 1fr);
}
.hero__events {
  position: relative;
  display: flex;
  flex-direction: column;
  z-index: 10;
  padding-top: 411px;
  padding-right: calc(11px + 134 * (100vw - 1024px) / 896);
  padding-bottom: calc(30px + 30 * (100vw - 1024px) / 896);
  padding-left: calc(30px + 30 * (100vw - 1024px) / 896);
  background: url(../img/photo-events.webp) center/cover no-repeat;
}
.hero__title {
  margin-bottom: 20px;
  font-size: 36px;
  line-height: 104%;
  color: var(--white-color);
}
.hero__btn {
  align-self: flex-start;
  text-align: center;
  transition: outline 0.2s ease-in-out, background-color 0.2s ease-in-out;
  outline-offset: -1px;
  outline: var(--hover-color) solid 3px;
  padding: 16px 22px 15px;
  background: var(--hover-color);
  color: var(--white-color);
}
.hero .entire-btn::after {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
.hero__descr {
  color: var(--white-color);
}
.hero__subtitle {
  margin-bottom: 11px;
  font-size: 24px;
  line-height: 104%;
  color: var(--main-black);
}
.hero .item-1 {
  grid-column: span 2;
}

.about-us {
  padding-top: 30px;
  padding-bottom: calc(132px + -63 * (100vw - 1025px) / 895);
  background-color: var(--main-black);
}
.about-us__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, calc(50% - 15px));
  justify-content: space-between;
}
.about-us__title {
  grid-column: span 2;
  margin-bottom: 5px;
  font-size: 24px;
  line-height: 31px;
  color: var(--white-color);
}
.about-us__descr {
  font-size: 16px;
  line-height: 28px;
  color: var(--cacaca-color);
}
.about-us__photo-cam {
  position: relative;
  padding-top: 255px;
  margin-bottom: 22px;
}
.about-us__photo-cam::after {
  content: "";
  position: absolute;
  top: -3px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: url(../img/about-us-cam.webp) center/cover no-repeat;
}
.about-us__form {
  display: flex;
}
.about-us__caption {
  display: block;
  margin-bottom: 16px;
  font-size: 24px;
  line-height: 22px;
  color: var(--white-color);
}
.about-us__form-label {
  position: relative;
  margin-right: 30px;
  flex-grow: 1;
}
.about-us__form-input {
  width: 100%;
  padding: 17px 23px 18px;
  background-color: #4a4a4a;
  outline: none;
  border: none;
  transition: color 0.2s ease-in-out, outline 0.2s ease-in-out;
}
.about-us__btn {
  padding: 15.5px 22px;
  color: var(--white-color);
  background: var(--hover-color);
}

.projects {
  padding-top: 68px;
  padding-bottom: 68px;
}
.projects__title {
  margin-bottom: 12px;
  font-size: 24px;
  line-height: 31px;
  color: var(--main-black);
}
.projects__all {
  display: grid;
  row-gap: calc(15px + 16 * (100vw - 1024px) / 896);
}
.projects__top, .projects__bottom {
  display: grid;
  gap: calc(15px + 15 * (100vw - 1024px) / 896);
}
.projects__top {
  grid-template-columns: repeat(6, 1fr);
}
.projects__bottom {
  grid-template-columns: repeat(4, 1fr);
}
.projects__top-item, .projects__bottom-item {
  position: relative;
}
.projects__figcaption {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  max-width: 196px;
}
.projects__link {
  margin-top: 18px;
  margin-bottom: 10px;
  color: var(--main-black);
}
.projects__day {
  font-size: 12px;
  line-height: 22px;
  color: var(--999-color);
}
.projects__subtitle {
  margin-bottom: 6px;
  font-size: 24px;
  line-height: 24px;
  color: var(--white-color);
}
.projects__descr {
  color: var(--white-color);
}
.projects__btn {
  align-self: flex-start;
  padding: 15px 22px;
  border: 3px solid var(--hover-color);
  color: var(--white-color);
}
.projects .huge-item-top {
  grid-column: span 3;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 22px;
  padding-bottom: 34px;
  padding-left: 30px;
  min-height: 300px;
  padding-right: calc(210px + 95 * (100vw - 1024px) / 896);
  background: url(../img/projects-big-1.webp) center/cover no-repeat;
}
.projects .huge-item-bottom {
  grid-column: span 2;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 22px;
  padding-bottom: 34px;
  padding-left: 30px;
  min-height: 300px;
  padding-right: calc(210px + 95 * (100vw - 1024px) / 896);
  background: url(../img/projects-big-2.webp) center/cover no-repeat;
}

.contacts {
  padding-bottom: 69px;
}
.contacts__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}
.contacts__title {
  grid-column: 1/-1;
  margin-bottom: 15px;
  color: var(--main-black);
}
.contacts__left {
  position: relative;
  max-height: 465px;
  min-height: 465px;
  max-width: 100%;
  grid-column: span 2;
  padding-top: 7px;
}
.contacts__map-wrap {
  position: relative;
}
.contacts__address {
  position: relative;
  z-index: 10;
  width: 50%;
  min-height: 460px;
  padding: 50px;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 175px 57px 150px 60px;
}
.contacts__subtitle {
  margin-bottom: 5px;
  font-size: 24px;
  line-height: 22px;
  color: var(--white-color);
}
.contacts__adr {
  font-style: normal;
  font-size: 16px;
  line-height: 28px;
  color: var(--cacaca-color);
}
.contacts__phone {
  display: flex;
  font-size: 16px;
  line-height: 21px;
  color: var(--white-color);
}
.contacts__map {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #e6e6fa;
}
.contacts__right, .contacts__form {
  display: flex;
  flex-direction: column;
}
.contacts__input {
  width: 100%;
  margin-bottom: 20px;
}
.contacts__textarea {
  resize: none;
  max-height: 150px;
}
.contacts__input, .contacts__textarea {
  padding: 15px 23px 16px 23px;
  border: none;
  background: var(--e9e9e9-color);
  color: var(--999-color);
}
.contacts__caption {
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;
  color: var(--main-black);
}
.contacts__content {
  margin-bottom: 14px;
  color: var(--999-color);
}
.contacts__btn {
  align-self: flex-start;
  padding: 16px 22px 15px;
  margin-top: 20px;
  outline-offset: -1px;
  font-size: 16px;
  outline: var(--hover-color) solid 3px;
  background-color: var(--hover-color);
  color: var(--white-color);
  transition: outline 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.contacts [class*=ground-pane] {
  -webkit-filter: grayscale(1);
  filter: grayscale(1);
}

.footer {
  background-color: var(--main-black);
}
.footer__container {
  justify-content: space-between;
  padding-top: 25px;
  padding-bottom: 25px;
}
.footer__container, .footer__left, .footer__right, .footer__social {
  display: flex;
}
.footer__left {
  flex-direction: column;
}
.footer__left .logo {
  width: 190px;
  height: 31px;
  margin-bottom: 6px;
}
.footer__left .footer__company {
  font-size: 14px;
  line-height: 18px;
  color: var(--e9e9e9-color);
}
.footer__mid {
  width: 368px;
}
.footer__quote {
  color: var(--e9e9e9-color);
}
.footer__name {
  color: #4c4c4c;
}
.footer__right {
  flex-direction: column;
  align-items: center;
}
.footer__social {
  flex-wrap: wrap;
}
.footer__more {
  margin-bottom: 8px;
  color: var(--cacaca-color);
}
.footer__social-item:not(:last-child) {
  margin-right: 20px;
}

@media (max-width: 1199.9px) {
  .header__list-item:not(:last-child) {
    margin-right: 33px;
  }
  .header__social-list {
    display: none;
  }
  .header__search-btn {
    display: block;
  }

  .search-img {
    display: inline-block;
    width: 25px;
    height: 25px;
  }
}
@media (max-width: 850px) {
  .header__list {
    display: none;
  }

  .bottom {
    display: block;
    background-color: #4A4A4A;
  }
  .bottom__container {
    padding-top: 12px;
    padding-bottom: 12px;
  }
  .bottom__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .bottom__list-item {
    font-size: 16px;
    line-height: 21px;
    color: var(--white-color);
    transition: color 0.3s ease;
  }
  .bottom__list-item:hover {
    color: var(--hover-color);
  }
  .bottom__list-item:focus-visible {
    background-color: var(--focus-color);
  }
  .bottom__list-item:active {
    color: var(--active-color);
  }
}
@media (max-width: 530px) {
  .bottom__list {
    display: block;
    padding: 70px 40px 40px 25px;
  }
  .bottom__list-item:not(:last-child) {
    margin-bottom: 30px;
  }
  .bottom__nav {
    display: block;
    position: absolute;
    top: 80px;
    left: 0;
    background: var(--main-black);
    width: 100%;
    box-shadow: 0 4px 2px -2px rgba(36, 36, 36, 0.17);
    left: -100%;
    transition: all 0.4s ease;
    z-index: 10;
  }
  .bottom__nav.active {
    left: 0;
  }
  .bottom__burger {
    display: block;
    position: relative;
    width: 20px;
    height: 20px;
    z-index: 50;
  }
  .bottom__burger span {
    position: absolute;
    background-color: var(--white-color);
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
  }
  .bottom__burger::before, .bottom__burger::after {
    content: "";
    background-color: var(--white-color);
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    -webkit-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
  }
  .bottom__burger::before {
    top: 2px;
  }
  .bottom__burger::after {
    width: 50%;
    bottom: 2px;
  }
  .bottom__burger.active {
    border: 1px solid transparent;
    border-radius: 100%;
    background-color: #808080;
  }
  .bottom__burger.active span {
    opacity: 0;
    transition: 0.2s;
  }
  .bottom__burger.active:before {
    width: 70%;
    height: 1px;
    transform: rotate(45deg);
    top: 9px;
    left: 2.6px;
    transition: 0.4s;
  }
  .bottom__burger.active:after {
    width: 70%;
    height: 1px;
    transform: rotate(-45deg);
    top: 8.5px;
    left: 2.6px;
    transition: 0.4s;
  }
}